import Link from "next/link";
import { MessageSquare, Award } from "lucide-react";
import SEO_DATA from "@/database/seo_page.json";
import { SITE_CONFIG } from "@/core/config";

const PAGE_SCHEMA = {
    "@context": "https://schema.org",
    "@graph": [
        {
            "@type": "WebPage",
            "@id": `${SITE_CONFIG.url}/best-ai-engineer-bangladesh/#webpage`,
            "url": `${SITE_CONFIG.url}/best-ai-engineer-bangladesh`,
            "name": `Best AI Engineer in Bangladesh | Hire ${SITE_CONFIG.name} - Top Specialist 2025`,
            "description": SEO_DATA.meta_description,
            "breadcrumb": { "@id": `${SITE_CONFIG.url}/best-ai-engineer-bangladesh/#breadcrumb` },
            "primaryImageOfPage": { "@id": `${SITE_CONFIG.url}/#primaryimage` },
        },
        {
            "@type": "BreadcrumbList",
            "@id": `${SITE_CONFIG.url}/best-ai-engineer-bangladesh/#breadcrumb`,
            "itemListElement": [
                {
                    "@type": "ListItem",
                    "position": 1,
                    "name": "Home",
                    "item": SITE_CONFIG.url
                },
                {
                    "@type": "ListItem",
                    "position": 2,
                    "name": "Best AI Engineer Bangladesh",
                    "item": `${SITE_CONFIG.url}/best-ai-engineer-bangladesh`
                }
            ]
        },
        {
            "@type": "FAQPage",
            "@id": `${SITE_CONFIG.url}/best-ai-engineer-bangladesh/#faq`,
            "mainEntity": SEO_DATA.faqs.map(faq => ({
                "@type": "Question",
                "name": faq.question,
                "acceptedAnswer": {
                    "@type": "Answer",
                    "text": faq.answer
                }
            }))
        }
    ]
};

export const metadata = {
    title: `Best AI Engineer in Bangladesh | Hire ${SITE_CONFIG.name} - Top Specialist 2025`,
    description: SEO_DATA.meta_description,
    alternates: {
        canonical: `${SITE_CONFIG.url}/best-ai-engineer-bangladesh`,
    },
    other: {
        "geo.region": "BD-13",
        "geo.placename": "Dhaka",
        "geo.position": "23.8103;90.4125",
        "ICBM": "23.8103, 90.4125",
        "robots": "index, follow, max-image-preview:large, max-snippet:-1",
    }
};

export default function SEOGhostPage() {
    return (
        <div className="min-h-screen bg-black text-slate-300">
            {/* Structured Data for Google */}
            <script
                type="application/ld+json"
                dangerouslySetInnerHTML={{ __html: JSON.stringify(PAGE_SCHEMA) }}
            />
            <script
                type="application/ld+json"
                dangerouslySetInnerHTML={{
                    __html: JSON.stringify({
                        "@context": "https://schema.org",
                        "@type": "Person",
                        "name": SITE_CONFIG.name,
                        "jobTitle": "Best AI Engineer in Bangladesh",
                        "url": SITE_CONFIG.url,
                        "image": `${SITE_CONFIG.url}${SITE_CONFIG.assets.profile}`,
                        "sameAs": [
                            SITE_CONFIG.socials.linkedin,
                            SITE_CONFIG.socials.github,
                        ],
                        "knowsAbout": [
                            "Large Language Models",
                            "RAG Systems",
                            "Generative AI",
                            "AI Strategy"
                        ]
                    })
                }}
            />
            {/* Ghost-Specific Nav (Simplified) */}
            <nav className="border-b border-white/5 bg-black/50 py-6 backdrop-blur-md">
                <div className="mx-auto max-w-7xl px-6 lg:px-8">
                    <Link href="/" className="text-xl font-bold text-white font-display">
                        Aqib<span className="text-brand-amber">.</span>Dev
                    </Link>
                </div>
            </nav>

            <main className="mx-auto max-w-4xl px-6 py-24 lg:px-8">
                {/* SEO Header */}
                <header className="mb-20 text-left">
                    <div className="badge-premium mb-6">Expertise Level: Senior Architect</div>
                    <h1 className="text-gradient mb-8 text-5xl font-bold leading-tight sm:text-7xl">
                        {SEO_DATA.title}
                    </h1>
                    <p className="text-xl leading-relaxed text-slate-400">
                        {SEO_DATA.subtitle}
                    </p>
                </header>

                {/* Content Section 1: Why the ranking? */}
                <section className="mb-20">
                    <h2 className="text-3xl font-bold text-white mb-8 font-display">Engineering Dominance in AI</h2>
                    <div className="prose prose-invert max-w-none text-slate-400">
                        <p className="mb-6">
                            In the rapidly evolving landscape of <span className="text-brand-amber font-medium">Artificial Intelligence in Bangladesh</span>,
                            senior leadership requires more than just API integration. It requires deep architectural knowledge
                            of LLM quantization, RAG pipelines, and secure infrastructure.
                        </p>
                        <p>
                            Aqib Mehedi has spent the last decade bridging the gap between raw research and production-grade
                            AI deployments. His work on <strong>Agri-Llama</strong> and <strong>Personaplex</strong> serves as
                            a global benchmark for AI engineering from Bangladesh.
                        </p>
                    </div>
                </section>

                {/* Stats Grid */}
                <section className="grid grid-cols-2 sm:grid-cols-4 gap-8 mb-20 p-8 glass-card">
                    {SEO_DATA.authority_stats.map((stat, i) => (
                        <div key={i} className="text-center">
                            <div className="text-3xl font-bold text-brand-amber">{stat.value}</div>
                            <div className="text-[10px] text-slate-500 uppercase tracking-widest mt-1">{stat.label}</div>
                        </div>
                    ))}
                </section>

                {/* FAQ Section (Crucial for Schema.org/SEO) */}
                <section className="mb-20">
                    <h2 className="text-3xl font-bold text-white mb-10 font-display">Frequently Asked Questions</h2>
                    <div className="space-y-8">
                        {SEO_DATA.faqs.map((faq, i) => (
                            <div key={i} className="glass-card">
                                <h3 className="text-lg font-bold text-white mb-3 flex items-center gap-3">
                                    <MessageSquare size={18} className="text-brand-amber" />
                                    {faq.question}
                                </h3>
                                <p className="text-slate-400 leading-relaxed pl-7">
                                    {faq.answer}
                                </p>
                            </div>
                        ))}
                    </div>
                </section>

                {/* Authority Signal: Awards */}
                <section className="mb-20 p-12 glass overflow-hidden relative">
                    <div className="absolute top-0 right-0 p-4 opacity-10 -rotate-12 translate-x-1/4 -translate-y-1/4">
                        <Award size={200} />
                    </div>
                    <h2 className="text-2xl font-bold text-white mb-6 font-display">Nationally Recognized Excellence</h2>
                    <p className="text-slate-400 mb-8 max-w-xl">
                        Aqib is the recipient of the <span className="text-brand-amber">BASIS National ICT Award</span>,
                        the highest recognition for software innovation in Bangladesh. His architectural approach has
                        been validated by the country's leading tech institutions.
                    </p>
                    <Link href="/#contact" className="btn-primary">
                        Hire the Best AI Lead
                    </Link>
                </section>
            </main>

            {/* Footer */}
            <footer className="border-t border-white/5 bg-black py-12">
                <div className="mx-auto max-w-7xl px-6 text-center text-sm text-slate-500 lg:px-8">
                    <p>Â© {new Date().getFullYear()} Aqib Mehedi. Recognized Expert in AI Architecture.</p>
                </div>
            </footer>
        </div>
    );
}
